	margin-top: -16px;
}

.o-form-suffix {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	flex-basis: auto;
	flex-grow: 0;
	flex-shrink: 1;
	margin: 8px 0 0 0;
}

.o-form-trail {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	flex-basis: 24px;
	flex-grow: 0;
	flex-shrink: 1;
	margin: 0 0 0 16px;
}

.is-invalid > .o-form-trail {
	color: var(--red-500);
}

.o-form-help-text {
	font-size: 0.75rem;
	letter-spacing: 0.0333333333em;
	font-weight: 400;
	line-height: 1;
	color: var(--gray-900);
	display: block;
	width: auto;
	margin-top: -32px;
	padding: 8px 8px 12px;
}

.is-invalid ~ .o-form-help-text {
	color: var(--red-500);
}

.o-avatar {
	vertical-align: middle;
	border: 2px solid #ffffff;
	border-radius: 50%;
}

.o-avatar-wrap {
	position: relative;
	width: 60%;
	margin: 0 auto;
}

.o-avatar-online {
	background-color: var(--lime-700);
	border: 2px solid #ffffff;
	border-radius: 50%;
	position: absolute;
	z-index: 101;
	top: 10%;
	right: 0;
	float: left;
	padding: 8%;
}

.o-avatar-small {
	display: inline-flex;
	width: 40px;
	height: 40px;
}

.o-avatar-rate-icon {
	margin-right: 0;
}

.o-avatar-rate-good, .o-avatar-rate-bad {
	border: 2px solid #ffffff;
	border-radius: 50%;
	color: #ffffff;
	position: absolute;
	z-index: 101;
	bottom: 0;
	float: left;
	padding: 8px;
}

.o-avatar-rate-good {
	background-color: var(--green-700);
}

.o-avatar-rate-good:hover, .o-avatar-rate-good:focus {
	background-color: var(--green-500);
	color: #ffffff;
	transition: 0.5s;
}

.o-avatar-rate-bad {
	background-color: var(--red-700);
	right: 0;
}

.o-avatar-rate-bad:hover, .o-avatar-rate-bad:focus {
	background-color: var(--red-500);
	color: #ffffff;
	transition: 0.5s;
}

.o-btn {
	font-size: 1rem;
	letter-spacing: 0.009375em;
	font-weight: 700;
	line-height: 1.5;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	text-align: center;
	vertical-align: middle;
	text-decoration: none;
	background-color: var(--gray-1200);
	border: none;
	border-radius: 2px;
	outline: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	color: var(--gray-900);
	position: relative;
	overflow: hidden;
	box-sizing: border-box;
	min-width: 64px;
	margin: 0;
	padding: 16px;
	user-select: none;
	transition: box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1), background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1), color 0.2s cubic-bezier(0.4, 0, 0.2, 1);
	will-change: transform, opacity;
}

.o-btn-icon {
	margin-right: 8px;
}

.o-btn-icon + .o-btn,
.o-btn-text + .o-btn-icon {
	margin-right: 0;
	margin-left: 8px;
}

.o-btn:active {
	outline: none;
}

.o-btn:hover {
	text-decoration: none;
	background-color: var(--gray-1000);
	color: var(--blue-700);
	cursor: pointer;
}

.o-btn:focus {
	outline: none;
}

.o-btn:disabled {
	background-color: transparent;
	box-shadow: none;
	cursor: default;
	pointer-events: none;
}

.o-btn::-moz-focus-inner {
	border: 0;
	padding: 0;
}

.o-icon {
	vertical-align: middle;
	width: 24px;
	height: 24px;
}

.o-icon.fa {
	font-size: 24px;
	vertical-align: baseline;
}

.o-container,
.o-container-centered {
	position: relative;
	max-width: 100%;
	margin-right: auto;
	margin-left: auto;
	padding-right: 12px;
	padding-left: 12px;
}

@media (min-width: 770px) {
	.o-container,
	.o-container-centered {
		padding-right: 16px;
		padding-left: 16px;
	}
}

@media (min-width: 411px) {
	.o-container,
	.o-container-centered {
		width: 411px;
	}
}

@media (min-width: 540px) {
	.o-container,
	.o-container-centered {
		width: 540px;
	}
}

@media (min-width: 770px) {
	.o-container,
	.o-container-centered {
		width: 770px;
	}
}

@media (min-width: 960px) {
	.o-container,
	.o-container-centered {
		width: 960px;
	}
}

@media (min-width: 1140px) {
	.o-container,
	.o-container-centered {
		width: 1140px;
	}
}

.o-continer-centered {
	text-align: center;
}

@keyframes grow {
	0% {
		opacity: 0;
		transform: scaleX(0.1);
	}
	50% {
		transform: scaleX(0.99);
	}
	100% {
		opacity: 1;
		transform: scaleX(1);
	}
}

@keyframes shrink {
	0% {
		opacity: 1;
		transform: scaleX(1);
	}
	50% {
		transform: scaleX(0.01);
	}
	100% {
		opacity: 0;
		transform: scaleX(0);
	}
}

.o-bar {
	background-color: var(--gray-1200);
	display: grid;
	grid-template-columns: 1fr 1fr;
}

.o-bar-right {
	display: flex;
	flex-direction: row;
	align-self: flex-end;
	margin-left: auto;
}

.o-bar-title {
	font-size: 1.25rem;
	letter-spacing: 0.0125em;
	font-weight: 500;
	line-height: 1;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	margin-bottom: 0;
	padding: 16px;
}

.o-bar-info {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	color: var(--gray-50);
	padding: 16px;
	padding-left: 0;
}

.o-bar-info:first-of-type {
	padding-left: 16px;
}

.o-bar-item-text {
	padding-left: 8px;
}

.o-bar-avatar {
	display: inline-block;
	width: 40px;
	height: 40px;
	padding: 0;
}

.o-bar-logo {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	position: relative;
	overflow: hidden;
	padding: 16px;
}

.o-bar-logo-icon {
	width: 64px;
	height: auto;
	max-height: 24px;
}

.o-bar-profile {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	max-width: 296px;
	padding: 8px 16px;
}

.o-bar-user {
	font-size: 1rem;
	letter-spacing: 0.03125em;
	font-weight: 500;
	line-height: 1.35;
	padding: 0 8px;
}

.o-bar-user-title {
	font-size: 0.75rem;
	letter-spacing: 0.0333333333em;
	font-weight: 400;
	line-height: 1.25rem;
	color: var(--gray-50);
	opacity: 0.54;
	display: block;
}

.o-bar-search {
	background-color: var(--gray-1200);
	opacity: 0;
	position: absolute;
	z-index: 999;
	float: left;
	width: 100%;
	transform: scaleX(0);
	transform-origin: right;
	animation: shrink 200ms cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.o-bar-search.is-active {
	animation: grow 400ms cubic-bezier(0.4, 0, 1, 1) forwards;
}

.o-bar-search-form {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	width: 100%;
	margin: 0;
}

.o-bar-search-toggle {
	font-size: 1rem;
	letter-spacing: 0.009375em;
	font-weight: 700;
	line-height: 1.5;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	color: var(--blue-700);
	flex-shrink: 0;
	margin-right: 0;
	margin-left: 0;
	padding: 16px;
}

.o-bar-search-action {
	font-size: 1rem;
	letter-spacing: 0.009375em;
	font-weight: 700;
	line-height: 1.5;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	color: var(--gray-900);
	margin-right: 0;
	margin-left: auto;
	padding: 16px;
}

.o-bar-search-action:hover, .o-bar-search-action:focus {
	color: var(--blue-700);
}

.o-bar-search-feild {
	font-size: 1rem;
	letter-spacing: 0.009375em;
	font-weight: 700;
	line-height: 1.5;
	background-color: transparent;
	background-image: none;
	border: 0;
	color: var(--gray-900);
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	padding: 16px;
}

.o-bar-search-feild:hover, .o-bar-search-feild:focus {
	background-color: transparent;
	color: var(--blue-700);
}

.o-action {
	color: var(--gray-900);
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	padding: 16px;
}

.o-action:hover, .o-action:focus {
	background-color: var(--gray-1000);
	outline: 0;
	color: var(--blue-700);
	transition: 0.5s;
}

.o-action-text {
	display: none;
}

@media (min-width: 540px) {
	.o-action-text {
		display: inline-flex;
	}
}

.o-action-icon + .o-action-text,
.o-action-text + .o-action-icon {
	padding-left: 8px;
}

.o-action-badge {
	background-color: var(--red-700);
	border-radius: 50px;
	color: var(--red-700);
	overflow: hidden;
	align-self: center;
	width: 10px;
	height: 12px;
	margin-left: 4px;
}

@media (min-width: 540px) {
	.o-action-badge {
		color: var(--gray-50);
		display: inline-flex;
		width: auto;
		height: auto;
	}
}

.o-modal {
	background-color: var(--gray-1300);
	border-radius: 2px;
	opacity: 0;
	width: calc(100% - (8px * 4));
	min-width: 280px;
	max-width: 865px;
	transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
	transform: scale(1.2);
}

.o-modal-mask {
	background-color: rgba(0, 0, 0, 0.38);
	position: fixed;
	z-index: 999;
	top: 0;
	left: 0;
	display: none;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}

.o-modal-mask.is-active {
	display: inline-flex;
}

.o-modal-mask.is-active .o-modal {
	opacity: 1;
	transition: all 0.3s ease;
	transform: scale(1);
}

.o-modal-header {
	display: flex;
	align-items: center;
	padding: 24px 24px 0;
}

.o-modal-header-title {
	font-size: 1.25rem;
	letter-spacing: 0.0125em;
	font-weight: 500;
	line-height: 2rem;
	flex: 1;
	margin: 0;
}

.o-modal-body {
	font-size: 1rem;
	letter-spacing: 0.03125em;
	font-weight: 500;
	line-height: 1.5rem;
	color: var(--gray-900);
	margin-top: 24px 0 0;
	padding: 0 24px 24px;
}

@media (min-width: 770px) {
	.o-modal-body {
		line-height: 1.5;
	}
}

.o-modal-footer {
	flex-wrap: wrap;
	justify-content: flex-end;
	align-items: center;
	padding: 8px;
}

.o-modal-action {
	background-color: var(--gray-900);
	color: #ffffff;
	margin-right: 8px;
}

.o-modal-action:hover {
	background-color: var(--blue-700);
	color: #ffffff;
}

@media (min-width: 770px) {
	.o-modal {
		min-width: 640px;
	}
}
